import { UniformConfig } from '..';

import { getEnv, getBoolEnv } from '..';

export function parseUniformConfig(env: any, runtime: boolean = true): UniformConfig {
    const UNIFORM_API_URL = getEnv(env, 'UNIFORM_API_URL', runtime ? undefined : '');
    const UNIFORM_API_SITENAME = getEnv(env, 'UNIFORM_API_SITENAME', runtime ? undefined : '');
    const UNIFORM_CONTENT_URL = getEnv(env, 'UNIFORM_CONTENT_URL', UNIFORM_API_URL);
    const UNIFORM_API_MAPSERVICE = getEnv(
        env,
        'UNIFORM_API_MAPSERVICE',
        '/uniform/api/content/${UNIFORM_API_SITENAME}/map'
    );

    const UNIFORM_OPTIONS_PREVIEW = getBoolEnv(env, 'UNIFORM_OPTIONS_PREVIEW', true);
    const UNIFORM_OPTIONS_DEBUG = getBoolEnv(env, 'UNIFORM_OPTIONS_DEBUG', false);
    const UNIFORM_OPTIONS_PREFETCH_LINKS = getBoolEnv(env, 'UNIFORM_OPTIONS_PREFETCH_LINKS', false);

    return {
        UNIFORM_API_URL,
        UNIFORM_API_SITENAME,
        UNIFORM_CONTENT_URL,
        UNIFORM_API_MAPSERVICE,

        UNIFORM_OPTIONS_PREVIEW,
        UNIFORM_OPTIONS_DEBUG,
        UNIFORM_OPTIONS_PREFETCH_LINKS,
    };
}
